.page {
  font-family: $font-fam;
  color: $main-dark;

  @include def-text-decoration;

  scroll-behavior: smooth;

  &:has(.page__menu:target) {
    overflow: hidden;
  }


  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transition: all 0.3s;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}
